
'use'
import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import * as d3 from 'd3';
import { useMutation } from '@tanstack/react-query';

// Define response data types
interface TokenizeData {
  title: string;
  tokens: { [key: string]: number };
}
interface StopwordData {
  title: string;
  words: { [key: string]: number };
}
interface StemData {
  title: string;
  words: { [key: string]: number };
}

const UploadDocument = () => {
  const [title, setTitle] = useState('');
  const [language, setLanguage] = useState('en');
  const [text, setText] = useState('');
  const [tokenizeData, setTokenizeData] = useState<TokenizeData | null>(null);
  const [stopwordData, setStopwordData] = useState<StopwordData | null>(null);
  const [stemData, setStemData] = useState<StemData | null>(null);
  const [file, setFile] = useState<File | null>(null);

  const tokenRef = useRef<HTMLDivElement>(null);
  const stopwordRef = useRef<HTMLDivElement>(null);
  const stemRef = useRef<HTMLDivElement>(null);

  const renderTable = (
      containerRef: React.RefObject<HTMLDivElement | null>,
      data: { [key: string]: number },
      headerLabel: string
    ) => {
    const container = d3.select(containerRef.current);
    container.selectAll('*').remove();
    const table = container.append('table').style('border-collapse', 'collapse').style('width', '100%');
    const thead = table.append('thead');
    const tbody = table.append('tbody');

    thead.append('tr')
      .selectAll('th')
      .data(['Word', headerLabel])
      .enter()
      .append('th')
      .text(d => d)
      .style('border', '1px solid #ccc')
      .style('padding', '8px')
      .style('background-color', '#f2f2f2');

    const rows = tbody.selectAll('tr')
      .data(Object.entries(data))
      .enter()
      .append('tr');
    
    rows.selectAll('td')
      .data(d => d)
      .enter()
      .append('td')
      .text(d => d)
      .style('border', '1px solid #ccc')
      .style('padding', '8px');
  };

  const tokenizeMutation = useMutation<TokenizeData, Error, void, unknown>({
    mutationFn: async () => {
      const response = await axios.post<TokenizeData>('http://127.0.0.1:8000/api/documents/tokenize', {
        title, language, original_text: text
      });
      return response.data;
    },
    onSuccess: (data: TokenizeData) => {
      setTokenizeData(data);
    }
  });

  const stopwordMutation = useMutation<StopwordData, Error, void, unknown>({
    mutationFn: async () => {
      const response = await axios.post('http://127.0.0.1:8000/api/documents/stopword', {
        title,
        tokens: tokenizeData?.tokens
      });
      return response.data;
    },
    onSuccess: (data: StopwordData) => {
      setStopwordData(data);
    }
  });

  const stemMutation = useMutation<StemData, Error, void, unknown>({
    mutationFn: async () => {
      const response = await axios.post<StemData>('http://127.0.0.1:8000/api/documents/stem', {
        title,
        words: stopwordData?.words
      });
      return response.data;
    },
    onSuccess: (data: StemData) => {
      setStemData(data);
    }
  });

  const handleFileUpload = () => {
    if (file) {
      const formData = new FormData();
      // Using default title and language as file upload doesn't wait for text inputs
      formData.append('file', file);
      formData.append('title', "Untitled File");
      formData.append('language', "en");
      axios
        .post('http://127.0.0.1:8000/api/documents/upload', formData, {
          headers: { 'Content-Type': 'multipart/form-data' },
        })
        .then(response => {
          setTokenizeData(response.data);
        })
        .catch(error => {
          console.error(error);
        });
    } else {
      console.error("No file selected.");
    }
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Existing text upload branch: if no file selected, use text & title
    tokenizeMutation.mutate();
  };

  useEffect(() => {
    if (tokenizeData && tokenRef.current) {
      renderTable(tokenRef, tokenizeData.tokens, 'Count');
    }
  }, [tokenizeData]);

  useEffect(() => {
    if (stopwordData && stopwordRef.current) {
      renderTable(stopwordRef, stopwordData.words, 'Count');
    }
  }, [stopwordData]);

  useEffect(() => {
    if (stemData && stemRef.current) {
      renderTable(stemRef, stemData.words, 'Count');
    }
  }, [stemData]);

  return (
    <div>
      <h2>Upload Document</h2>
      {/* Existing text upload form */}
      <form onSubmit={handleSubmit}>
        <input type="text" placeholder="Title" value={title} onChange={e => setTitle(e.target.value)} required />
        <select value={language} onChange={e => setLanguage(e.target.value)}>
          <option value="en">English</option>
          <option value="am">Amharic</option>
        </select>
        <textarea placeholder="Enter document text" value={text} onChange={e => setText(e.target.value)} required />
        <button type="submit">Upload and Tokenize</button>
      </form>

      {/* New file upload section */}
      <div style={{ marginTop: '20px' }}>
        <h3>Upload File Directly</h3>
        <input type="file" onChange={e => setFile(e.target.files ? e.target.files[0] : null)} />
        <button onClick={handleFileUpload}>Upload File</button>
      </div>

      <div>
        <h3>Tokens</h3>
        <div ref={tokenRef} />
      </div>

      {tokenizeData && (
        <div>
          <button onClick={() => stopwordMutation.mutate()}>Remove Stop Word</button>
          <div>
            <h3>Stopword Removed</h3>
            <div ref={stopwordRef} />
          </div>
        </div>
      )}

      {stopwordData && (
        <div>
          <button onClick={() => stemMutation.mutate()}>Stem and Normalize</button>
          <div>
            <h3>Stemmed and Normalized</h3>
            <div ref={stemRef} />
          </div>
        </div>
      )}
    </div>
  );
};

export default UploadDocument;
